@keyframes wl-spinner-animate {
  0% {
    transform: scale(1); }
  50% {
    transform: scale(1.3); }
  100% {
    transform: scale(1); } }

.wl-spinner {
  margin: 20px;
  height: 20px;
  font-size: 0;
  text-align: center; }

.wl-spinner--running .wl-spinner__shape {
  animation-name: wl-spinner-animate; }

.wl-spinner__shape {
  display: inline-block;
  height: 20px;
  width: 20px;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-duration: 1.2s; }

.wl-spinner__shape--circle {
  fill: #007aff; }

.wl-spinner__shape--rect {
  fill: #fcc207;
  animation-delay: 0.4s; }

.wl-spinner__shape--hexagon {
  fill: #ff2222;
  animation-delay: 0.8s; }
